<template>
  <div>

    <h1 class="font-bold">Who sanctions whom in Russia</h1>
    
    <div class="inline-flex m-2 my-4 mt-6">

      <template>
        <v-text-field
          v-model="search"
          label="Search"
          class="mx-4"
        ></v-text-field>
      </template>

      <template>

        <v-select
          :items="selectors"
          label="Type"
          outlined
          v-model="selectedVal"
        ></v-select>

      </template>

    </div>

    <v-data-table
      :headers="headers"
      :items="rows"
      :items-per-page="5"
      class="elevation-1"
      :search="search"
      :custom-filter="filterOnlyCapsText"
    ></v-data-table>

    

  </div>
</template>

<script>

import axios from 'axios';
import csvToJson from 'csvtojson';

export default {
    methods: {
      load() {
        axios
          .get(
            'https://docs.google.com/spreadsheets/d/1b9dGS8ACKZ1WsYchlpqQt7EBMD3QZhTPIZWG2n70Grc/export?format=csv&gid=0',
            { responseType: 'blob',}
          )
          .then( (response) => {
            const file = response.data;
            file.text().then((csvStr) => {
              csvToJson()
                .fromString(csvStr)
                .then((jsonObj) => {
                  this.rows = jsonObj;
                  console.log(this.rows);
                });

            });
          })
      },
      filterOnlyCapsText (value, search) {
        return value != null &&
          search != null &&
          typeof value === 'string' &&
          value.toString().toLocaleUpperCase().indexOf(search) !== -1
      },
    },
    created() {
      this.load();
    },
    data () {
      let search =  "";
      let info = "";
      let headers = [
        {
          text: 'Entity/Individual',
          align: 'start',
          sortable: false,
          value: 'entity_individual',
        },
        { text: 'Australia', value: 'Australia' },
        { text: 'Canada', value: 'Canada' },
        { text: 'EU', value: 'EU' },
        { text: 'Switzerland', value: 'Switzerland' },
        { text: 'UK', value: 'UK' },
        { text: 'US', value: 'US' },
        { text: 'Sectors for Entities as a filter', 
          value: 'filter',
          filter: value => {
              if (!this.selectedVal) return true
              return value == this.selectedVal
            },
        },
      ];
      let rows = [
        {
          entity_individual: '27th Scientific Center',
          Australia: '✅',
          Canada: '❌',
          EU: '❌',
          Switzerland: '❌',
          UK: '❌',
          US: '❌',
          filter: 'I',
        }
      ];
      let selectors = ["I", "O"];
      let selectedVal = "";
      return {
        info,
        headers,
        rows,
        search,
        selectors,
        selectedVal
      }
    },
  }
</script>

<style scoped>

* {
  font-family: "Proxima", monospace;
}

</style>
